
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
components: {
XHeader
},
props: {
title: String
},
data() {
return {
};
},
methods: {

onBack() {
this.$emit("on-back");
this.$router ? this.$router.back() : window.history.back();
}, //返回主页
onHomeMenu() {
this.$emit("on-homeMenu");
this.$router.push({
name: "index"
});
}
}
};



